@import '../../common/styles/sass/mixins.scss';

.headerContainer {
    position:fixed;
    top:0;
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color:rgba($color:  rgb(94, 91, 91), $alpha: 0.7);
    z-index: 100;
    transition: all 0.5s ease 0s;

    &::after{
      content: 'My Portfolio';
      position: fixed;
      top: 3px;
      left: 10px;
      width: 100%;
      height: 100%;
      font-size: 70px;
      text-align: start;
      font-weight: bolder;
      color: rgba(233, 233, 233, 0.3);
      text-shadow: 6px 0 7px rgba(177, 236, 169, 0.3),
        -6px 0 5px rgba(177, 236, 169, 0.3) ;
       transform: scaleX(100%);

       @include max-bp('strech') {
        top: 10px;
        font-size: 57px;
       }
       @include max-bp('mini') {

        font-size: 43px;
       }
      
    }

    &:hover {
      box-shadow: 0 13px 25px rgb(250, 250, 250);
      background-color:rgba($color:  rgb(49, 48, 48), $alpha: 0.9);
      
    }

    @include max-bp('mini') {
       
      height: 150px;     
            
    }


  .mobileNavMenu{
    display: none;

    @include max-bp('small') {
       
      position: fixed;
      bottom: 20px;
      right: 20px;
      font-size: 33px;
      display: block;
      opacity: 0.5;
      cursor: pointer;
      z-index: 120;
      color:azure; 
      &:hover {
        opacity: 1;
      }  
  }
}

.navbarContainer {
  width: 100%;
  display:flex;


  @include max-bp('small') {
    display: none;
  }

}

  .navbarMobileContainer{
    display:none;

    @include max-bp('small') {

      position: fixed;
      top: 0;
      left: 0;
      display: flex;
      justify-content: flex-end;
      width: 100vw;
      height: 103vh;
      background-color: rgba(37, 85, 97, 0.8);
      z-index: 110;
      
    }
  }
}